// ** React Imports
import { Fragment, useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import axios from '@src/configs/axios/axiosConfig'
// ** Reactstrap Imports
import {
    Row,
    Col,
    Card,
    Label,
    Input,
    Table,
    Modal,
    Button,
    CardBody,
    ModalBody,
    ModalHeader,
    FormFeedback
} from 'reactstrap'

import * as yup from "yup"
import { yupResolver } from "@hookform/resolvers/yup"
// ** Third Party Components
import { File, Trash2 } from 'react-feather'
import { useForm, Controller } from 'react-hook-form'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'

// ** Custom Components
import { getData, addRole, updateRole, deleteRole, addRolePermissions, updateRolePermissions } from './store/roles' //deleteRole
// ** FAQ Illustrations
import { activeOrganizationid, orgUserId } from '@src/helper/sassHelper'
import illustration from '@src/assets/images/illustration/faq-illustrations.svg'
import { useDispatch, useSelector } from 'react-redux'
import toast from 'react-hot-toast'
import Permissions from './Permissions'
const activeOrgId = activeOrganizationid()
const userId = orgUserId()

const Roles = ({dataTabId}) => {
    // ** States
    const MySwal = withReactContent(Swal)
    const [show, setShow] = useState(false)
    const [data, setData] = useState([])
    const [modalType, setModalType] = useState('Add New')
    const store = useSelector(state => state.role)
    const dispatch = useDispatch()
    const [modules, setModules] = useState([])
    const [permissionArr, setPermissionArr] = useState([])
    const [formModal, setFormModal] = useState(false)
    const [selectedRoleId, setselectedRoleId] = useState(null)
    const [permissions, setPermissions] = useState([])

    const getModules = () => {
        axios.post('/modules/list').
            then((res) => {
                setModules(res.data.modules)
            }).catch(() => { })
    }

    const getRolePermissions = (roleId) => {
        axios.post('/rolepermissions/list', { roleId }).
            then((res) => {
                setPermissions(res.data.rolepermission.permissions.split(','))
            }).catch(() => { })
    }

    // ** Hooks
    const schema = yup.object().shape({
        organizationId: yup.string().default(activeOrgId),
        name: yup.string().required('Please Enter Role'),
        description: yup.string().nullable(),
        panel: yup.string().default('client'),
        isDefault: yup.number().default(1),
        isAdminRole: yup.boolean().default(false),
        updatedBy: yup.string().default(userId),
        createdBy: yup.string().default(userId),
        permissionsArr: yup.array().min('1')
    })

    const { handleSubmit, formState: { errors }, control, reset, setValue } = useForm({
        resolver: yupResolver(schema),
        defaultValues: schema.cast()
    })

    const onReset = async () => {
        setShow(false)
        await dispatch(getData())
        reset({ id: '', name: '', description: '' })
    }

    const onSubmit = async data => {

        let eventname
        if (modalType === 'Edit') {
            await dispatch(updateRole(data))
            const obj = {
                organizationId: activeOrgId,
                roleId: data.id,
                permissions: data.permissionsArr.toString(),
                updatedBy: userId
            }

            await dispatch(updateRolePermissions(obj))
            eventname = 'Updated'
        } else {
            const res = await dispatch(addRole(data))
            const obj = {
                organizationId: activeOrgId,
                roleId: res.payload.id,
                permissions: data.permissionsArr.toString(),
                createdBy: userId
            }
            await dispatch(addRolePermissions(obj))
            eventname = 'Added'
        }

        toast.success(`Role ${eventname} Successfully`, { position: 'bottom-right' })
        onReset()
    }

    const handleCheckbox = (mod, trans) => {
        const slug = `${mod}-${trans}`
        let permissions_array = [...permissionArr]
        if (permissionArr.indexOf(slug) === -1) {
            permissions_array = [...permissionArr, slug]
        } else {
            permissions_array.splice(permissionArr.indexOf(slug), 1)
        }
        setPermissionArr(permissions_array)
        setValue('permissionsArr', permissionArr)
    }
    useEffect(() => {
        setData(store.data)
    }, [store.data])


    const renderPermissions = () => {

        return (
            modules.map((module, index) => {
                const transactions = module.transactions.split(',')
                return (
                    <tr key={index}>
                        <td className='text-nowrap fw-bolder'>{module.name}</td>
                        <td>
                            <div className='d-flex'>
                                <div className='form-check me-3 me-lg-5'>
                                    <Input type='checkbox' id={`read-${module.slug}`} onClick={() => handleCheckbox(module.slug, 'read')} value={`${module.slug}-read`} defaultChecked={permissions.indexOf(`${module.slug}-read`) > -1} disabled={transactions.indexOf('read') === -1} />
                                    <Label className='form-check-label' for={`read-${module.slug}`}>
                                        Read
                                    </Label>
                                </div>
                                <div className='form-check me-3 me-lg-5'>
                                    <Input type='checkbox' id={`write-${module.slug}`} onClick={() => handleCheckbox(module.slug, 'write')} value={`${module.slug}-write`} defaultChecked={permissions.indexOf(`${module.slug}-write`) > -1} disabled={transactions.indexOf('write') === -1} />
                                    <Label className='form-check-label' for={`write-${module.slug}`}>
                                        Write
                                    </Label>
                                </div>
                                <div className='form-check me-3 me-lg-5'>
                                    <Input type='checkbox' id={`delete-${module.slug}`} onClick={() => handleCheckbox(module.slug, 'delete')} value={`${module.slug}-delete`} defaultChecked={permissions.indexOf(`${module.slug}-delete`) > -1} disabled={transactions.indexOf('delete') === -1} />
                                    <Label className='form-check-label' for={`delete-${module.slug}`}>
                                        Delete
                                    </Label>
                                </div>
                                <div className='form-check me-3 me-lg-5'>
                                    <Input type='checkbox' id={`import-${module.slug}`} onClick={() => handleCheckbox(module.slug, 'import')} value={`${module.slug}-import`} defaultChecked={permissions.indexOf(`${module.slug}-import`) > -1} disabled={transactions.indexOf('import') === -1} />
                                    <Label className='form-check-label' for={`import-${module.slug}`}>
                                        import
                                    </Label>
                                </div>
                                <div className='form-check'>
                                    <Input type='checkbox' id={`export-${module.slug}`} onClick={() => handleCheckbox(module.slug, 'export')} value={`${module.slug}-export`} defaultChecked={permissions.indexOf(`${module.slug}-export`) > -1} disabled={transactions.indexOf('export') === -1} />
                                    <Label className='form-check-label' for={`export-${module.slug}`}>
                                        Export
                                    </Label>
                                </div>
                            </div>
                        </td>
                    </tr>
                )
            })
        )
    }


    const editRole = (role) => {
        reset(role)
        getRolePermissions(role.id)        
        setModalType('Edit')
        setShow(true)
        renderPermissions()
    }

    const deletefn = (id) => {
        return MySwal.fire({
            title: 'Are you sure?',
            text: "You won't be able to revert this!",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Yes, delete it!',
            customClass: {
                confirmButton: 'btn btn-primary',
                cancelButton: 'btn btn-outline-danger ms-1'
            },
            buttonsStyling: false
        }).then(async (result) => {
            if (result.value) {
                await dispatch(deleteRole(id))
                MySwal.fire({
                    icon: 'success',
                    title: 'Deleted!',
                    text: 'Department has been deleted.',
                    customClass: {
                        confirmButton: 'btn btn-success'
                    }
                })
                await dispatch(getData())
                return true
            } else if (result.dismiss === MySwal.DismissReason.cancel) {
                return false
            }
        })
    }

    const handleModalClosed = () => {
        setModalType('Add New')
    }

    useEffect(async () => {
        if (dataTabId === 'roles') {
            await dispatch(getData())
            getModules()
        }

    }, [dataTabId])

    return (
        <Fragment>
            <Row>
                <Col xl={4} md={6}>
                    <Card>
                        <Row>
                            <Col sm={5}>
                                <div className='d-flex align-items-end justify-content-center h-100'>
                                    <img className='img-fluid mt-2' src={illustration} alt='Image' width={85} />
                                </div>
                            </Col>
                            <Col sm={7}>
                                <CardBody className='text-sm-end text-center ps-sm-0'>
                                    <Button
                                        color='primary'
                                        className='text-nowrap mb-1'
                                        onClick={() => {
                                            setModalType('Add New')
                                            setShow(true)
                                        }}
                                    >
                                        Add New Role
                                    </Button>
                                    <p className='mb-0'>Add a new role, if it does not exist</p>
                                </CardBody>
                            </Col>
                        </Row>
                    </Card>
                </Col>
                {data.map((item, index) => {
                    return (
                        <Col key={index} xl={4} md={6}>
                            <Card>
                                <CardBody>
                                    {/* <div className='d-flex justify-content-between'>
                                        <span>{`Total ${item.totalUsers} users`}</span>
                                        <AvatarGroup data={item.users} />
                                    </div> */}
                                    <div className='d-flex justify-content-between align-items-end mt-1 pt-25'>
                                        <div className='role-heading'>
                                            <h4 className='fw-bolder'>{item.name}</h4>
                                            <h6>{item.description}</h6>
                                            {!item.isdefault &&
                                                <Link
                                                    to='/'
                                                    className='role-edit-modal'
                                                    onClick={e => {
                                                        e.preventDefault()
                                                        editRole(item)
                                                    }}
                                                >
                                                    <small className='fw-bolder'>Edit Role</small>
                                                </Link>}
                                            <File size={20} className='ms-1 text-primary cursor-pointer' onClick={() => { setselectedRoleId(item.id); setFormModal(true) }} />
                                        </div>
                                        {!item.isdefault &&
                                            <Trash2 className='font-medium-5' onClick={() => deletefn(item.id)} />
                                        }
                                    </div>
                                </CardBody>
                            </Card>
                        </Col>
                    )
                })}
            </Row>

            <Modal
                isOpen={formModal}
                toggle={() => setFormModal(!formModal)}
                className='modal-dialog-centered modal-lg'
            >
                <ModalHeader className='bg-transparent' toggle={() => setFormModal(!formModal)}>
                    Role Permissions
                </ModalHeader>
                <ModalBody>
                    <Permissions roleId={selectedRoleId} />
                </ModalBody>
            </Modal>
            <Modal
                isOpen={show}
                onClosed={handleModalClosed}
                toggle={() => setShow(!show)}
                className='modal-dialog-centered modal-lg'
            >
                <ModalHeader className='bg-transparent' toggle={() => setShow(!show)}></ModalHeader>
                <ModalBody className='px-2'>
                    <div className='text-center mb-1'>
                        <h1>{modalType} Role</h1>
                    </div>
                    <Row tag='form' onSubmit={handleSubmit(onSubmit)}>
                        <Col xs={12}>
                            <Label className='form-label' for='name'>
                                Role Name
                            </Label>
                            <Controller
                                name='name'
                                control={control}
                                render={({ field }) => (
                                    <Input {...field} id='name' placeholder='Enter Role name' invalid={errors.name && true} />
                                )}
                            />
                            {errors.name && <FormFeedback>Please enter a valid role name</FormFeedback>}
                        </Col>
                        <Col xs={12} className='mt-1'>
                            <Label className='form-label' for='description'>
                                Description
                            </Label>
                            <Controller
                                name='description'
                                control={control}
                                render={({ field }) => (
                                    <Input {...field} id='description' placeholder='Enter Description' invalid={errors.description && true} />
                                )}
                            />
                        </Col>
                        <Col xs={12}>
                            <h4 className='mt-2 pt-50'>Role Permissions</h4>
                            <Table className='table-flush-spacing' responsive>
                                <tbody>{
                                    permissions.length > 0 &&
                                    renderPermissions()
                                }

                                </tbody>
                            </Table>
                        </Col>
                        <Col className='text-center mt-2' xs={12}>
                            <Button type='submit' color='primary' className='me-1'>
                                Submit
                            </Button>
                            <Button type='reset' outline onClick={onReset}>
                                Discard
                            </Button>
                        </Col>
                    </Row>
                </ModalBody>
            </Modal>
        </Fragment>
    )
}

export default Roles
