// ** React Imports
import { Fragment, useState, useEffect } from 'react'

// ** Reactstrap Imports
import { Row, Col, TabContent, TabPane, Card, CardHeader, CardBody } from 'reactstrap'

// ** Demo Components
import Tabs from './Tabs'
import AttendanceScore from './AttendanceScore'
import BusinessHours from './BusinessHours'
import Holidays from './Holidays'
import LeaveSetting from './LeaveSetting'

// ** Styles
import '@styles/react/libs/flatpickr/flatpickr.scss'
import '@styles/react/pages/page-account-settings.scss'
import { useParams } from 'react-router-dom'

const Attendance = () => {
  // ** States

  const [activeTab, setActiveTab] = useState('')
  const { tabIdsub } = useParams()

  useEffect(() => {

    setActiveTab(tabIdsub)

  }, [tabIdsub])

  return (
    <Card>
      <CardHeader>
        <Tabs className='mb-2' activeTab={activeTab} />
      </CardHeader>
      <CardBody>
        <Fragment>
          <Row>
            <Col xs={12}>
              <TabContent activeTab={activeTab}>
                <TabPane tabId='attendancescore'>
                  <AttendanceScore data={activeTab} />
                </TabPane>
                <TabPane tabId='businesshours'>
                  <BusinessHours data={activeTab} />
                </TabPane>
                <TabPane tabId='holidays'>
                  <Holidays data={activeTab} />
                </TabPane>
                <TabPane tabId='leavesetting'>
                  <LeaveSetting data={activeTab} />
                </TabPane>
              </TabContent>
            </Col>
          </Row>
        </Fragment>
      </CardBody>
    </Card>
  )
}

export default Attendance
