// ** React Imports
import { Fragment, useEffect, useState } from 'react'
import Select from 'react-select'
// ** Reactstrap Imports
import axios from '@src/configs/axios/axiosConfig'
import {
  Row,
  Col,
  Card,
  Form,
  Label,
  Input,
  Button,
  CardBody,
  FormFeedback,
  Table,
  Modal,
  ModalHeader,
  ModalBody
} from 'reactstrap'
import { useForm, Controller } from "react-hook-form"
import * as yup from "yup"
import { yupResolver } from "@hookform/resolvers/yup"
import { getData, addOrgPreference } from './store/index'
import { getLeaveTypes, addLeaveTypes, updateLeaveTypes, deleteLeaveTypes } from './store/leavesettings'
import { activeOrganizationid, orgUserId } from '@src/helper/sassHelper'

import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'

const activeOrgId = activeOrganizationid()
const userId = orgUserId()
// ** Third Party Components
import classnames from 'classnames'
import { useDispatch, useSelector } from 'react-redux'
import toast from 'react-hot-toast'

const leaveTypeOptions = [{ id: '1', name: "Paid" }, { id: 2, name: 'Non Paid' }]
const leaveTypeArr = ["", "Paid", "Non Paid"]

const LeaveTypes = (tabId) => {
  const MySwal = withReactContent(Swal)
  const [data, setData] = useState([])
  const [selected, setSelected] = useState(null)
  const [designationOptions, setDesignationOptions] = useState([])
  const [designations, setDesignations] = useState([])
  const [formModal, setFormModal] = useState(false)
  const store = useSelector(state => state.leavesettings)

  const [excludeWeekend, setExcludeWeekend] = useState(null)
  const [excludeHolidays, setExcludeHolidays] = useState(null)

  const dispatch = useDispatch()

  const schema = yup.object().shape({
    organizationId: yup.string().default(activeOrgId),
    name: yup.string().required('Please Enter Name'),
    daysCount: yup.string().required('Please Enter Days Count'),
    leaveType: yup.string().required('Please Select Leave Type'),
    status: yup.number().default(1),
    designation: yup.array().min(1, 'Please Select Designation'),
    updatedBy: yup.string().default(userId),
    createdBy: yup.string().default(userId)
  })

  const { handleSubmit, formState: { errors }, control, setValue } = useForm({
    resolver: yupResolver(schema),
    defaultValues: schema.cast()
  })

  const closeModal = () => {
    setFormModal(false)
  }
  useEffect(() => {
    setFormModal(false)
  }, [])


  const getDesignation = () => {
    axios.post('/designations/dropdown').
      then((res) => {
        const arr = res.data.designations
        arr.unshift({ id: 0, name: 'Select All' })
        setDesignationOptions(arr)

      }).catch(() => { })
  }

  const deletefn = (id) => {
    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await dispatch(deleteLeaveTypes(id))
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'LeaveTypes has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
        return true
      } else if (result.dismiss === MySwal.DismissReason.cancel) {
        return false
      }
    })
  }

  const resetForm = () => {

    setValue('organizationId', activeOrgId)
    setValue('name', '')
    setValue('daysCount', '')
    setValue('designation', [])
    setValue('id', '')
    setValue('leaveType', '')
    setValue('status', true)
    closeModal()
  }

  const onSubmit = async data => {

    data.designation.pop(0)
    if (selected !== null) {
      await dispatch(updateLeaveTypes(data))
      toast.success('Leave Types Updated Successfully', { position: 'bottom-right' })
      setSelected(null)
    } else {
      await dispatch(addLeaveTypes(data))
      toast.success('Leave Types Added Successfully', { position: 'bottom-right' })
    }
    resetForm()
  }


  const updateDLeaveSetting = async (sts1, sts2) => {
    await addOrgPreference({ excludeWeekend: sts1, excludeHolidays: sts2, createdBy: userId, updatedBy: userId, organizationId: activeOrgId })
  }

  useEffect(() => {
    getDesignation()
  }, [])

  useEffect(async () => {
    if (tabId.data === 'leavesetting') {
      await dispatch(getLeaveTypes())
      const response = await dispatch(getData(activeOrgId))
      const res = response.payload.data
      if (res !== undefined) {
        setExcludeWeekend(res.excludeweekend || false)
        setExcludeHolidays(res.excludeholidays || false)
      }
    }

  }, [tabId])

  useEffect(() => {
    setData(store.data)
  }, [store.data])

  const getRow = (fieldLabel, fieldName, reqflag = true) => {
    return (
      <Col md={12}>
        <Label sm='12' className={classnames(`form-label ${reqflag ? 'required' : ''}`)} for={fieldName}>
          {fieldLabel}
        </Label>
        <Col>
          <Controller
            id={fieldName}
            name={fieldName}
            control={control}
            render={({ field }) => <Input invalid={errors[fieldName] && true} {...field} />}
          />
          {errors[fieldName] && <FormFeedback>{errors[fieldName].message}</FormFeedback>}
        </Col>
      </Col>
    )
  }

  const getSelectRow = (fieldLabel, fieldName, options, reqflag = false) => {
    return (

      <Col md={12}>
        <Label sm='12' className={classnames(`form-label ${reqflag ? 'required' : ''}`)} for={fieldName} >
          {fieldLabel}
        </Label>
        <Col>
          <Controller
            control={control}
            name={fieldName}
            id={fieldName}
            render={({ field, ref }) => (
              <Select
                inputRef={ref}
                className={classnames('react-select', { 'is-invalid': errors[fieldName] })}
                {...field}
                classNamePrefix='select'
                options={options}
                value={options.find(c => { return c.id === field.value })}
                onChange={val => { return field.onChange(val.id) }}
                getOptionLabel={(option) => option.name}
                getOptionValue={(option) => option.id}
              />
            )}

          />
          {errors[fieldName] && <FormFeedback className='text-danger'>{errors[fieldName]?.message}</FormFeedback>}
        </Col>
      </Col>

    )
  }

  const handleDesignationChange = (e) => {
    setDesignations(e)
    const tempArr = Array.isArray(e) ? e.map(x => x.id) : []
    const selectallIndex = tempArr.indexOf(0)
    if (selectallIndex > -1) {
      designationOptions.forEach((obj) => {
        tempArr.push(obj.id)
      })
    }
    const test = tempArr.filter((id) => id !== 0)
    setValue("designation", test)
  }

  const openEditModal = card => {
    setSelected(card)
    setFormModal(true)

    setValue('organizationId', activeOrgId)
    setValue('name', card.name)
    setValue('daysCount', card.dayscount)
    setValue('designation', card.designation)
    setValue('id', card.id)
    setValue('leaveType', card.leavetype)
    setValue('status', card.status)

  }

  const colourStyles = {
    multiValue: (styles, { }) => {

      return {
        ...styles,
        backgroundColor: '#7367f0'
      }
    }
  }

  return (
    <Fragment>
      <Card>
        <CardBody className='py-25'>
          <Row className='gx-4'>
            <Col className='d-flex align-items-center'>
              <h6 className='fw-bolder mb-2'>Leave Types</h6>
            </Col>
            <Col className='d-flex align-items-sm-center justify-content-xl-end justify-content-start flex-xl-nowrap flex-wrap flex-sm-row flex-column pe-xl-1 p-0 mt-xl-0 mt-1'>
              <Button color='primary' outline className='ms-1' onClick={() => { setFormModal(true) }}>
                Add
              </Button>
            </Col>
          </Row>
          <Table>
            <thead>
              <tr>
                <th>Name</th>
                <th>Leave Type</th>
                <th>No of Day's</th>
                <th>Action</th>
              </tr>
            </thead>
            <tbody>
              {data.map((card, index) => {
                return (
                  <tr key={index}>
                    <td>{card.name}</td>
                    <td>{leaveTypeArr[card.leavetype]}</td>
                    <td>{card.dayscount}</td>
                    <td>
                      <Button size='sm' outline color='primary' className='me-75' onClick={() => openEditModal(card)}>
                        Edit
                      </Button>
                      {!card.isPrimary &&
                        <Button size='sm' outline onClick={() => { deletefn(card.id) }}>Delete</Button>
                      }
                    </td>
                  </tr>
                )
              }
              )}
            </tbody>
          </Table>

          <Col lg='12' className='mt-2 mt-lg-0'>
            <h6 className='fw-bolder'>Leave Settings</h6>
            <div className='demo-inline-spacing'>
              {excludeWeekend !== null &&
                <div className='form-check form-switch'>
                  <Input type='switch' name='excludeWeekend' id='exampleCustomSwitch' defaultChecked={excludeWeekend} onChange={() => { updateDLeaveSetting(!excludeWeekend, excludeHolidays) }} />
                  <Label for='exampleCustomSwitch' className='form-check-label'>
                    Exclude Weekend for Leave Calculation
                  </Label>
                </div>
              }
              {excludeHolidays !== null &&
                <div className='form-check form-switch'>
                  <Input type='switch' name='excludeHolidays' id='exampleCustomSwitchDisabled' defaultChecked={excludeHolidays} onChange={() => { updateDLeaveSetting(excludeWeekend, !excludeHolidays) }} />
                  <Label for='exampleCustomSwitchDisabled' className='form-check-label'>
                    Exclude Holidays for Leave Calculation
                  </Label>
                </div>
              }
            </div>
          </Col>
        </CardBody>
      </Card>
      <Modal isOpen={formModal} toggle={() => setFormModal(!formModal)} className='modal-dialog-centered'>
        <ModalHeader toggle={() => closeModal()}>Holidays</ModalHeader>
        <ModalBody>
          {
            Object.keys(errors).map((key) => {

              return <FormFeedback key={key}>${errors[key]?.message}</FormFeedback>

            })
          }
          <Col lg='12'>
            {
              Object.keys(errors).map((key) => {

                return <FormFeedback key={key}>${errors[key]?.message}</FormFeedback>

              })
            }
            <Row tag={Form} className='gx-2 gy-1' onSubmit={handleSubmit(onSubmit)}>

              {getRow('Name', 'name', true)}

              {getSelectRow('Leave Type', 'leaveType', leaveTypeOptions, true)}

              {getRow('No of Days', 'daysCount', true)}

              {/* <Col md={3}>
                <Row className='form-switch form-check-success px-0'>
                  <Label className='form-label' for='status'>
                    Enabled?
                  </Label>
                  <Controller
                    id='status'
                    name='status'
                    control={control}
                    render={({ field }) => <Input type="switch" invalid={errors.status && true} onInput={(val) => field.onChange(val)} {...field} />}
                  />
                </Row>
              </Col> */}

              <Col md={12}>
                <Label sm='12' className={classnames(`form-label required`)} for={`designation`} >
                  Applicable Designation
                </Label>
                <Col>
                  <Controller
                    control={control}
                    name="designation"
                    id="designation"
                    render={({ field, ref }) => (
                      <Select
                        inputRef={ref}
                        className={classnames('react-select', { 'is-invalid': errors.designation })}
                        {...field}
                        classNamePrefix='select'
                        options={designationOptions}
                        isMulti={true}
                        onChange={handleDesignationChange}
                        value={designations} // set selected values
                        getOptionLabel={(option) => option.name}
                        getOptionValue={(option) => option.id}
                        styles={colourStyles}
                      />
                    )}

                  />
                  {errors['designation'] && <FormFeedback className='text-danger'>{errors['designation']?.message}</FormFeedback>}
                </Col>
              </Col>

              <Col className='mt-2 pt-1' xs={12}>
                <Button type='submit' className='me-1' color='primary'>
                  Submit
                </Button>
                <Button type='button' className='me-1' color='warning' onClick={() => closeModal()}>
                  Cancel
                </Button>
              </Col>
            </Row>
          </Col>
        </ModalBody>
      </Modal>
    </Fragment>
  )
}

export default LeaveTypes
