// ** React Imports
import {
  Card,
  CardBody,
  Input,
  Table
} from 'reactstrap'

// ** Third Party Components
import axios from '@src/configs/axios/axiosConfig'
import { useEffect, useState } from 'react'
import toast from 'react-hot-toast'
import { getData } from './store/index'
import { activeOrganizationid, orgUserId } from '@src/helper/sassHelper'
import { useDispatch } from 'react-redux'

const activeOrgId = activeOrganizationid()
const userId = orgUserId()

const taxRate = ({ tabId, loaderCallBack }) => {
  // ** States

  const setDefaultTax = (id) => {
    loaderCallBack(true)
    axios.post('/organizationprefernces/createandupdate', { organizationId: activeOrgId, updatedBy: userId, defaultTaxId: id })
      .then(() => {
        loaderCallBack(false)
        toast.success('Tax Rate Upated Successfully', { position: 'bottom-right' })
      })
      .catch(() => {
        toast.error('Event Failed!', { position: 'bottom-right' })
      })
  }
  const dispatch = useDispatch()
  const [data, setData] = useState([])
  const [defaulttaxid, setDefaultTaxId] = useState(null)

  const getTaxList = () => {
    axios.post('/taxgroups/list')
      .then((res) => {
        setData(res.data.taxgroups)
      })
      .catch((err) => { console.log(err) })
  }

  useEffect(async () => {
    if (tabId === 'taxrate') {
      const response = await dispatch(getData(activeOrgId))
      const res = response.payload.data
      setDefaultTaxId(res.defaulttaxid)

      await getTaxList()
    }

  }, [tabId])

  return (

    <Card>
      <CardBody className='py-25'>
        <Table>
          <thead>
            <tr>
              <th>Default</th>
              <th>Tax Rate</th>
              <th>Percentage</th>
            </tr>
          </thead>
          <tbody>
            {data.map((item, index) => {
              return (
                <tr key={index}>
                  <td>
                    <Input type='radio' value={item.id} name='taxrate' defaultChecked={defaulttaxid === item.id} onChange={() => { setDefaultTax(item.id) }} />
                  </td>
                  <td>{item.name}</td>
                  <td>{item.percentage || 0} %</td>
                </tr>
              )
            })
            }
          </tbody>
        </Table>
      </CardBody>
    </Card>

  )
}

export default taxRate
