import React, { useState, useEffect } from 'react'
import { GoogleLogin } from 'react-google-login'
import { Card, CardBody, CardHeader, TabContent, TabPane, Nav, NavItem, NavLink, Button, Row, Col } from 'reactstrap'
import { Mail, File } from 'react-feather'
import { gapi } from "gapi-script"
import axios from '@src/configs/axios/axiosConfig'
import { orgUserId } from '@src/helper/sassHelper'
import toast from 'react-hot-toast'
const userId = orgUserId()
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import { Link, useParams } from 'react-router-dom'

import GDriveImage from '@src/assets/images/icons/drive.png'
import GmailImage from '@src/assets/images/icons/gmail.png'
const ConnectionsTabContent = () => {

  const { tabId } = useParams()
  const MySwal = withReactContent(Swal)
  const SCOPE = 'https://www.googleapis.com/auth/drive https://www.googleapis.com/auth/drive.appdata https://www.googleapis.com/auth/drive.file https://www.googleapis.com/auth/drive.resource https://www.googleapis.com/auth/drive.metadata https://www.googleapis.com/auth/drive.metadata.readonly https://www.googleapis.com/auth/drive.photos.readonly https://www.googleapis.com/auth/drive.readonly https://www.googleapis.com/auth/userinfo.email https://www.googleapis.com/auth/userinfo.email https://www.googleapis.com/auth/gmail.send https://mail.google.com/'

  const [active, setActive] = useState('')
  const [profile, setProfile] = useState([])
  const [logindata, setLOginData] = useState({})
  const [connectionScope, setConnectionScope] = useState(SCOPE)
  const clientId = process.env.REACT_APP_GOOGLE_CLIENT_ID
  console.log(profile)
  const onSuccessLogout = () => {

    const auth2 = gapi.auth2.getAuthInstance()
    auth2.signOut().then(function () {
      setProfile({})
      axios.post('/google/logout', { type: '1' })
        .then((res) => { console.log(res) })
        .catch((err) => { console.log(err) })
    })

    MySwal.fire({
      icon: 'success',
      title: 'Deleted!',
      text: 'Successfully Logout.',
      customClass: {
        confirmButton: 'btn btn-success'
      }
    })
  }

  useEffect(() => {

    if (tabId === 'integration') {
      gapi.load('client:auth2', () => {
        gapi.client.init({
          clientId,
          scope: connectionScope
        })
      })
    }

  }, [tabId, connectionScope, active])


  const onSuccess = (res) => {
    if (res?.code !== undefined) {
      setLOginData(res)
    }
  }

  useEffect(() => {
    if (tabId === 'integration') {
      axios.post('/google/getlogin', { type: '1' })
        .then((res) => {
          setProfile(res.data.data)
        })
        .catch((err) => { console.log(err) })
    }
  }, [tabId])

  useEffect(() => {
    if (tabId === 'integration') {

      if (logindata.code !== undefined) {
        axios.post('/google/googleredirect', { type: '2', code: logindata.code, createdBy: userId })
          .then((res) => {
            setProfile(res.data.user)
            toast.success('Email Linked Successfully', { position: 'bottom-right' })
          })
          .catch((err) => { console.log(err) })
      }
    }
  }, [tabId])

  const onFailure = (err) => {
    console.log('failed', err)
  }

  const logoutFn = () => {
    return false
    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, Logout!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        onSuccessLogout()
      }
    })
  }

  useEffect(() => {
    setActive('1')
    logoutFn()
    onSuccess()
    onFailure()
    setConnectionScope(SCOPE)
  }, [])

  return (
    <Card>
      <CardBody>
        <h5>Apps</h5>
        <hr></hr>
        <Row >
          <Col className='d-flex align-items-center'>
            <img className='rounded border p-md-50' height={35} width={35} src={GmailImage}
              alt="G Mail" />
          </Col>
          <Col>
            <p className='p-md-25 m-0'>GMail</p>
            <p className='p-md-25 m-0'>by<span className='ms-md-50 text-primary'>Google</span></p>
            <p className='p-md-25 m-0'>Status : <span className='text-success'>Connected</span></p>

            <h6 className='p-md-25 m-0'>Login</h6>
            <p className='p-md-25 m-0'> Email : madhan@tuia.in<span></span></p>
          </Col>
          <Col>
            <h6 className='m-0'>Features</h6>
            <p className='m-0'> Send Automated Mail to client through GMail </p>
          </Col>
          <Col className='d-flex align-items-center justify-content-end'>
            {/* <Button color='primary'>Connect</Button> */}
            <Button color='danger'>Revoke</Button>
          </Col>
        </Row>
        <hr></hr>
        <Row>
          <Col className='d-flex align-items-center'>
            <img className='rounded border  p-md-50' height={35} width={35} src={GDriveImage}
              alt="Google Drive" />
          </Col>
          <Col>
            <p className='p-md-25 m-0'>Google Drive</p>
            <p className='p-md-25 m-0'>by<span className='ms-md-50 text-primary'>Google</span></p>
            <p className='p-md-25 m-0'>Status : <span className='text-success'>Connected</span></p>

            <h6 className='p-md-25 m-0'>Login</h6>
            <p className='p-md-25 m-0'> Email : madhan@tuia.in<span></span></p>
          </Col>
          <Col>
            <h6 className='m-0'>Features</h6>
            <p className='m-0'> Save Files in Google Drive Storage </p>
          </Col>
          <Col className='d-flex align-items-center justify-content-end'>
            {/* <Button color='primary'>Connect</Button> */}
            <Button color='danger'>Revoke</Button>
          </Col>
        </Row>
        <hr></hr>
      </CardBody>
    </Card>
  )
}
export default ConnectionsTabContent