// ** React Imports
import { Fragment, useEffect, useState } from 'react'


// ** Reactstrap Imports
import { Row, Col, TabContent, TabPane, Card, CardBody } from 'reactstrap'

// ** Demo Components
import Tabs from './Tabs'
import Designation from './Designation'
import Department from './Department'
import Role from './Roles'

// ** Styles
import '@styles/react/libs/flatpickr/flatpickr.scss'
import '@styles/react/pages/page-account-settings.scss'
import { useParams } from 'react-router-dom'

const RolesAndDesignation = () => {
  // ** States

  const { tabIdsub } = useParams()

  const [activeTab, setActiveTab] = useState('')

  useEffect(() => {
    setActiveTab(tabIdsub)
  }, [tabIdsub])

  return (
    <Card>
      <CardBody>
        <Fragment>

          <Row>
            <Col xs={12}>
              <Tabs className='mb-2' activeTab={activeTab} />

              <TabContent activeTab={activeTab}>
                <TabPane tabId='roles'>
                  <Role dataTabId={activeTab} />
                </TabPane>

                <TabPane tabId='designation'>
                  <Designation dataTabId={activeTab} />
                </TabPane>

                <TabPane tabId='department'>
                  <Department dataTabId={activeTab} />
                </TabPane>

              </TabContent>

            </Col>
          </Row>

        </Fragment>
      </CardBody>
    </Card>
  )
}

export default RolesAndDesignation
