// ** React Imports
import { Fragment, useState, useEffect } from 'react'
// ** Reactstrap Imports
import { Row, Col, TabContent, TabPane, Card, CardHeader } from 'reactstrap'

// ** Demo Components
import Tabs from './Tabs'
import GeneralSetting from './GeneralSetting'
import InvoiceAccounts from './InvoiceAccounts'
//import GSTSetting from './GSTSetting'
import TaxRateSetting from './TaxRateSetting'
import ExemptionReasonSetting from './ExemptionReasonSetting'

// ** Styles
import '@styles/react/libs/flatpickr/flatpickr.scss'
import '@styles/react/pages/page-account-settings.scss'
import { useParams } from 'react-router-dom'

const InvoiceSettings = (props) => {

  const { loaderCallBack } = props
  const [activeTab, setActiveTab] = useState('')
  const { tabIdsub } =  useParams()

  useEffect(() => {
    setActiveTab(tabIdsub)
  }, [tabIdsub])

  return (
    <Card>
      <CardHeader>
        <Fragment>
          
            <Row>
              <Col xs={12}>
                <Tabs className='mb-2' activeTab={activeTab}  />

                <TabContent activeTab={activeTab}>
                  <TabPane tabId='general'>
                    <GeneralSetting tabId={activeTab} loaderCallBack={loaderCallBack} />
                  </TabPane>

                  <TabPane tabId='invoiceaccount'>
                    <InvoiceAccounts tabId={activeTab} loaderCallBack={loaderCallBack} />
                  </TabPane>

                  <TabPane tabId='taxrate'>
                    <TaxRateSetting tabId={activeTab} loaderCallBack={loaderCallBack} />
                  </TabPane>

                  <TabPane tabId='exemptionreason'>
                    <ExemptionReasonSetting tabId={activeTab} loaderCallBack={loaderCallBack} />
                  </TabPane>

                </TabContent>

              </Col>
            </Row>
        </Fragment>
      </CardHeader>
    </Card>
  )
}

export default InvoiceSettings
