// ** React Imports
import { Fragment, useState, useEffect } from 'react'

// ** Third Party Components
import axios from 'axios'

// ** Reactstrap Imports
import { Row, Col, TabContent, TabPane, Card } from 'reactstrap'

// ** Demo Components
import Tabs from './Tabs'
import InvoiceSettings from './Invoice/index'
import Attendance from './Attendance/index'
import AccountTabContent from './AccountTabContent'
import RolesAndDesignation from './RolesAndDesignation/index'
import ConnectionsTabContent from './ConnectionsTabContent'
import EmailTemplates from './EmailTemplates'
import BillingCurrentPlan from './BillingCurrentPlan'

import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'

// ** Styles
import '@styles/react/libs/flatpickr/flatpickr.scss'
import '@styles/react/pages/page-account-settings.scss'
import { useParams } from 'react-router-dom'

const AccountSettings = () => {
  // ** States
  const [activeTab, setActiveTab] = useState('edit')
  const [data, setData] = useState(null)
  const { tabId, tabIdsub } = useParams()


  const toggleTab = tab => {
    setActiveTab(tab)
  }

  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  const loaderCallBack = (val) => {
    setBlock(val)
  }

  useEffect(() => {
    let tab = tabId
    if (tabId === undefined && tabIdsub === undefined) {
      tab = 'edit'
    }
    setActiveTab(tab)

  }, [tabId, tabIdsub])


  useEffect(() => {
    axios.get('/account-setting/data').then(response => setData(response.data))
  }, [])

  return (
    <Fragment>
      {/* <Breadcrumbs title='Account Settings' data={[{ title: 'Pages' }, { title: 'Account Settings' }]} /> */}
      {data !== null ? (
        <Row>
          <UILoader blocking={block} loader={<Loader />}>
            <Col xs={12}>
              <Card className='pt-1 ps-1'>
                <Tabs className='mb-2' activeTab={activeTab} toggleTab={toggleTab} />
              </Card>
              <TabContent activeTab={activeTab}>
                <TabPane tabId='edit'>
                  <AccountTabContent data={data.general} mainTab={activeTab} loaderCallBack={loaderCallBack} />
                </TabPane>
                <TabPane tabId='invoicesetting'>
                  <InvoiceSettings mainTab={activeTab} loaderCallBack={loaderCallBack} />
                </TabPane>
                <TabPane tabId='attendancesetting'>
                  <Attendance mainTab={activeTab} loaderCallBack={loaderCallBack} />
                </TabPane>
                <TabPane tabId='rolesanddesignation'>
                  <RolesAndDesignation mainTab={activeTab} loaderCallBack={loaderCallBack} />
                </TabPane>
                <TabPane tabId='emailtemplates'>
                  <EmailTemplates mainTab={activeTab} loaderCallBack={loaderCallBack} />
                </TabPane>
                <TabPane tabId='billing'>
                  <BillingCurrentPlan mainTab={activeTab} loaderCallBack={loaderCallBack} />
                </TabPane>
                <TabPane tabId='integration'>
                  <ConnectionsTabContent mainTab={activeTab} loaderCallBack={loaderCallBack} />
                </TabPane>
              </TabContent>
            </Col>
          </UILoader>
        </Row>
      ) : null}
    </Fragment>
  )
}

export default AccountSettings
