import React, { useEffect, useState } from 'react'
import { Card, CardBody, Table } from 'reactstrap'
import axios from '@src/configs/axios/axiosConfig'
import { Edit } from 'react-feather'
import { useParams } from 'react-router-dom'
// import { orgUserId } from '@src/helper/sassHelper'
// import toast from 'react-hot-toast'
// const userId = orgUserId()
// import Swal from 'sweetalert2'
// import withReactContent from 'sweetalert2-react-content'

const EmailTemplates = () => {

  const [dataList, setDataList] = useState([])
  const {tabId}  = useParams()
  useEffect(() => {

    if (tabId === 'emailtemplates') {
      axios.post('/emailtemplates/list')
        .then((res) => {
          setDataList(res.data.emailtemplates)
        })
        .catch(err => {
          console.log(err)
        })
    }

  }, [tabId])

  return (
    <Card>

      <CardBody>
        <Table>
          <thead>
            <tr>
              <td>Name</td>
              <td>Description</td>
              <td>Updated By</td>
              <td>Actions</td>
            </tr>
          </thead>
          <tbody>
            {
              dataList.map((obj, key) => {
                return (
                  <tr key={key}>
                    <td>{obj.name}</td>
                    <td>
                      <div dangerouslySetInnerHTML={{ __html: obj.description }}></div>
                    </td>
                    <td>{obj.updatedy}</td>
                    <td>
                      <Edit size={14} />
                    </td>
                  </tr>
                )
              })
            }
          </tbody>
        </Table>
      </CardBody>
    </Card>
  )
}
export default EmailTemplates